<!-- 课程预约 -->
<template>
	<div class="container">
		<div class="course-title flex">
			<div>课程预约</div>
			<el-select v-model="room" placeholder="请选择教室">
				<el-option v-for="item in roomOption" :label="item.room_name" :value="item.room_id"></el-option>
			</el-select>
		</div>
		<div class="box">
			<div class="flex-between">
				<div class="flex" style="margin: 0 auto;">
					<ul class="type-wrap flex">
						<li v-for="item in type" class="flex">
							<div class="type-lump" :style="{'background':item.color}"></div>
							<div>{{item.text}}</div>
						</li>
					</ul>
				</div>
				<div>
					<order-time @time-change="timeSelected"></order-time>
				</div>
			</div>
			<order-calendar :add="true" :current="time" :tableData="courseData" :type="2"></order-calendar>
		</div>
	</div>
</template>

<script>
import calendar from '../../../components/calendar'
import time from '../../../components/time-selected'
export default {
    data() {
        return {
            type: [{
                color: '#4C84FF',
                text: '同步课'
            }, {
                color: '#FDA423',
                text: '录播课'
            }],
            courseData: [{
                x: 2,
                y: 2,
                name: '生物组织与细胞力学',
                subject: '生物',
                school: '成都七中',
                author: '龙飞',
                time: '8:40-9:20',
                state: 3,
                type: 1
            }],
            room:'',
            roomOption:[],
            time:new Date()
        }
    },
    components: {
        'order-calendar': calendar,
        'order-time': time
    },
    created() {
        this.roomSearch()
        this.courseSearch()
    },
    watch:{
        room(){
            this.courseSearch()
        }
    },
    methods: {
        roomSearch() {
            this.$api.orderCourseRoom().then(res => {
                this.roomOption = res.data
            })
        },
        courseSearch(){
            let params = {};
            if(this.room != ""){
                params = {
				    date:this.$myLayer.timeFormat(this.time).substring(0,10),
                    roomcode:this.room
                }
            }else{
                params = {
				    date:this.$myLayer.timeFormat(this.time).substring(0,10)
                }
            }
            this.$api.orderCourseSearch(params).then(res => {
                res.data.map(v => {
                    v.section = v.courseSection
                    v.date = this.getWeekDay(v.courseDate)
                })
                this.courseData = res.data
            })
        },
        timeSelected(value) {
		    this.time = value;
		    this.courseSearch();
        },
        getWeekDay(params){
            let time = new Date(params).getDay()
            return time == 0?7:time
        }
    }
}
</script>

<style lang="scss" scoped="scoped">
	.container {
		padding: 0 32px;
		background-color: #F1F3F8;
		min-height: calc(100vh - 60px);

		.box {
			background: rgba(255, 255, 255, 1);
			box-shadow: 0px 5px 20px 0px rgba(23, 38, 71, 0.05);
			border-radius: 6px;
			padding: 22px 24px;
			width: 100%;

			.flex-between {
				align-items: center;
				margin-bottom: 30px;

				ul {
					li {
						align-items: center;
						margin-right: 24px;

						.state-lump {
							width: 16px;
							height: 16px;
							border-radius: 4px;
							margin-right: 12px;
						}

						.type-lump {
							width: 24px;
							height: 16px;
							border-radius: 4px;
							margin-right: 12px;
						}
					}
				}
			}
		}

		.course-title {
			padding-top: 20px;
			margin-bottom: 24px;
			align-items: center;

			div {
				&:first-child {
					color: #55595C;
					font-weight: bold;
					margin-right: 10px;
				}
			}
		}
	}
</style>
