<!-- 时间选择 -->
<template>
	<div class="time-selected-wrap">
		<i class="iconfont icon-arrow-down icon-arrow-left" @click="time(1)"></i>
		<el-date-picker v-model="value" type="date" :clearable="false" @change="timeChange"></el-date-picker>
		<i class="iconfont icon-arrow-down" @click="time(0)"></i>
	</div>
</template>

<script>
export default {
    data(){
        return{
            value: '',
        }
    },
    created() {
        this.value = new Date()
    },
    methods:{
        timeChange(val){
            this.$emit('time-change',val)
        },
        time(type){
			const week = 1000*60*60*24*7
			type?this.value = new Date(this.value.getTime() - week):this.value = new Date(this.value.getTime() + week)
            this.$emit('time-change',this.value)
        }
    }
}
</script>

<style>
	.time-selected-wrap .el-icon-date{
		display: none;
	}
	.time-selected-wrap .el-date-editor--date{
		width: 78px!important;
		padding: 0!important;
	}
	.time-selected-wrap .el-input__inner{
		padding: 0!important;
		border: none;
	}
</style>
<style lang="scss" scoped="scoped">
	.icon-arrow-left{
		display: inline-block;
		transform: rotate(180deg);
	}
	.time-selected-wrap{
		display: flex;
		align-items: center;
	}
</style>
