<!-- 日程表格 -->
<template>
	<table class="table">
		<thead>
			<tr>
				<td style="width: 56px" v-if="type!=3">{{new Date(current).getFullYear()}}</td>
				<td v-for="(item,index) in 7" :class="timeWeek == item&&isWeek()?'date-active':''">
					<p>{{weekDays[index]}}</p>
					<p>{{weekList[index]}}</p>
				</td>
			</tr>
		</thead>
		<tbody>
			<tr v-for="(item,index) in num">
				<td v-if="type!=3" :style="{'background':'#F6F8FA','border':'none!important'}">第{{index+1}}节</td>
				<td v-for="(item1,index1) in 7" :style="{'height':type==1?'52px':'77px'}">
					<div class="count" v-if="isData(item,item1)&&type==1" :style="{'background-color':`rgba(76,132,255,${0.1*(isTrue(isData(item,item1).recordSections) + isTrue(isData(item,item1).classesSections))})`}">
						{{isTrue(isData(item,item1).recordSections) + isTrue(isData(item,item1).classesSections)}}
						<ul>
							<li>录播:{{isTrue(isData(item,item1).recordSections)}}</li>
							<li>同步:{{isTrue(isData(item,item1).classesSections)}}</li>
							<!-- <li>会议:{{isData(item,item1).meeting}}</li> -->
						</ul>
					</div>
					<div class="count" style="line-height: 77px;" v-if="isData(item,item1)&&type==5" :style="{'background-color':`rgba(76,132,255,${0.1*(isTrue(isData(item,item1).num))})`}">
						{{isData(item,item1).num}}
					</div>
					<div class="course-add" v-if="!isData(item,item1)&&add">
						<i class="iconfont icon-add"></i>
					</div>
					<div class="course-detail" @mouseenter="typeTwoShow" @mouseleave="typeTwoHide" v-if="isData(item,item1)&&type==2" :style="{'background-color':isData(item,item1).courseOrder==8002?'#4C84FF':'#FDA423'}">
						<div>{{isData(item,item1).courseName}}</div>
						<div>{{isData(item,item1).userName}}</div>
						<div class="course-detail-col" @mouseenter="typeTwoShow1" @mouseleave="typeTwoHide1">
							<div class="flex">
								<div>{{isData(item,item1).courseName}}</div>
								<div>{{status(isData(item,item1).status)}}</div>
							</div>
							<div class="flex">
								<div>{{isData(item,item1).subjectName}}</div>
								<div>{{isData(item,item1).userName}}</div>
								<div>{{isData(item,item1).courseTime}}</div>
							</div>
						</div>
					</div>
					<div class="my-calendar" @mouseenter="typeTwoShow" @mouseleave="typeTwoHide" v-if="isData(item,item1)&&type==3" :class="isData(item,item1).courseOrder==8002?'bg1':isData(item,item1).courseOrder==8001?'bg2':'bg3'">
						<div>{{isData(item,item1).courseName}}</div>
						<div>{{isData(item,item1).courseTime}}</div>
						<div class="course-detail-col" @mouseenter="typeTwoShow1" @mouseleave="typeTwoHide1">
							<div class="flex">
								<div>{{isData(item,item1).courseName}}</div>
								<div>{{status(isData(item,item1).status)}}</div>
							</div>
							<div class="flex">
								<div>{{isData(item,item1).organizationName}}</div>
								<div>{{isData(item,item1).courseTime}}</div>
							</div>
							<div class="entry" v-if="isData(item,item1).courseOrder!=8001&&isData(item,item1).status==9011">
								进入{{isData(item,item1).courseOrder==8002?'课堂':'会议'}}
							</div>
						</div>
					</div>
					<div class="school-detail" @mouseenter="typeTwoShow" @mouseleave="typeTwoHide" v-if="isData1(item,item1).length!=0&&type==4">
						<div class="school-detail-1" :style="{'background':isData1(item,item1)[0].courseOrder==8002?'#4C84FF':'#FDA423'}">
							<div>{{isData1(item,item1)[0].courseName}}</div>
							<div>{{isData1(item,item1)[0].userName}}</div>
						</div>
						<div class="school-detail-2" :style="{'background':isData1(item,item1)[1].courseOrder==8002?'rgba(76,132,255,0.64)':'rgba(253,164,35,0.64)'}" v-if="isData1(item,item1).length>=2">
							
						</div>
						<div class="course-detail-col" @mouseenter="typeTwoShow1" @mouseleave="typeTwoHide1">
							<ul>
								<li v-for="item2 in isData1(item,item1)">
									<div>
										<div>{{item2.courseName}}</div>
										<div>{{status(item2.status)}}</div>
									</div>
									<div>
										<div>{{item2.subjectName}}</div>
										<div>{{item2.userName}}</div>
										<div>{{item2.courseTime}}</div>
										<div v-if="item2.orderType" :class="item2.courseOrder==8002?'sync-icon':'rec-icon'">{{item2.orderType}}</div>
										<div v-if="item2.roomType" class="sync-icon">{{item2.roomType}}</div>
									</div>
									<div v-show="item2.status == 9011&&item2.courseOrder == 8002">
										<div class="course-entry">
											进入课程
										</div>
									</div>
								</li>
							</ul>
						</div>
					</div>
					<div class="my-calendar bg1" @mouseenter="typeTwoShow" @mouseleave="typeTwoHide" v-if="isData(item,item1)&&type==6">
						<div>{{isData(item,item1).courseName}}</div>
						<div>{{isData(item,item1).courseTime}}</div>
						<div class="course-detail-col" @mouseenter="typeTwoShow1" @mouseleave="typeTwoHide1">
							<div class="flex">
								<div>{{isData(item,item1).courseName}}</div>
								<div>{{status(isData(item,item1).status)}}</div>
							</div>
							<div class="flex">
								<div>{{isData(item,item1).subjectName}}</div>
								<div>{{isData(item,item1).userName}}</div>
								<div>{{isData(item,item1).courseTime}}</div>
							</div>
							<div class="entry" v-if="isData(item,item1).status==9011">
								进入课程
							</div>
						</div>
					</div>
				</td>
			</tr>
		</tbody>
	</table>
</template>

<script>
export default {
    props: {
        tableData: {
            type: Array,
            default: function() {
                return []
            }
        },
        num: {
            type: Number,
            default: 10
        },
        type: {
            type: Number,
            default: 1
        },
        add: {
            type: Boolean,
            default: false
        },
        current:{
            type: Date,
            default: function() {
                return new Date()
            }
        }
    },
    data() {
        return {
            weekDays: ['星期一', '星期二', '星期三', '星期四', '星期五', '星期六', '星期天'],
            weekList: [],
            oneDayLong: 24 * 60 * 60 * 1000
        }
    },
    created() {
        this.weekListDay(new Date())
        this.isWeek()
    },
    computed: {
        nowTime() {
            let date = new Date();
            return date;
        },
        timeWeek: function() {
            let index;
            switch (this.nowTime.getDay()) {
            case 0:
                index = 7;
                break;
            case 1:
                index = 1;
                break;
            case 2:
                index = 2;
                break;
            case 3:
                index = 3;
                break;
            case 4:
                index = 4;
                break;
            case 5:
                index = 5;
                break;
            case 6:
                index = 6;
                break;
            }
            return index;
        }
    },
    methods: {
        weekListDay(date) {
            let _date = [];
            let monday = this.monday(date);
            for (let i = 1; i < 8; i++) {
                let month = new Date(monday - (1 - i) * this.oneDayLong).getMonth() + 1;
                let _month = month >= 10 ? month : "0" + month;
                let day = new Date(monday - (1 - i) * this.oneDayLong).getDate();
                let _day = day >= 10 ? day : "0" + day;
                _date.push(_month + "-" + _day);
            }
            this.weekList = _date
        },
        monday(date) {
            let complete = date.getDay();
            let _nowTime = date.getTime();
			if(complete == 0){
				complete = 7
			}
            return new Date(_nowTime - (complete - 1) * this.oneDayLong)
        },
        isWeek() {
            let value = false;
            if (new Date(this.current).getTime() - this.oneDayLong / 3 - this.monday(this.nowTime).getTime() > -this.oneDayLong) {
                if (new Date(this.current).getTime() - this.monday(this.nowTime).getTime() < this.oneDayLong * 6) {
                    value = true
                }
            }
            return value;
        },
        isData(para, para1) {
            let value = false;
            this.tableData.map(v => {
                if (v.section == para && v.date == para1) {
                    value = v;
                }
            })
            return value;
        },
		isData1(para, para1) {
		    let value = [];
		    this.tableData.map(v => {
		        if (v.section == para && v.date == para1) {
		            value.push(v);
		        }
		    })
		    return value;
		},
        tdHeight(type) {
            let height = '52px'
            switch (type) {
            case 2:
                height = '120px'
                break;
            case 3:
                height = '100px'
                break;
            default:
                break;
            }
            return height;
        },
        isTrue(para){
            return para?Number(para):0
        },
        status(para){
            let str = '';
            switch (para){
            case 9010: str = '未开始';
                break;
            case 9011: str = '进行中';
                break;
            case 9012: str = '已结束';
                break;
            case 9013: str = '已上传';
                break;			
            default: str = '已发布';
                break;
            }
			return str;
        },
		typeTwoShow(){
			event.currentTarget.lastElementChild.style.display = "block"
		},
		typeTwoHide(){
			event.currentTarget.lastElementChild.style.display = "none"
		},
		typeTwoShow1(){
			event.currentTarget.style.display = "block"
		},
		typeTwoHide1(){
			event.currentTarget.style.display = "none"
		}
    },
    watch: {
        current: function(val) {
            this.weekListDay(new Date(val))
            this.isWeek()
        }
    }
}
</script>

<style lang="scss" scoped="scoped">
	.third-cor {
		width: 0;
		height: 0;
		border-width: 8px;
		border-style: solid;
		transform: rotate(45deg);
		position: absolute;
		left: -8px;
		top: -8px;
	}

	.no-start {
		border-color: transparent rgb(76, 132, 255) transparent transparent;
	}

	.starting {
		border-color: transparent rgb(94, 203, 61) transparent transparent;
	}

	.end {
		border-color: transparent rgb(183, 187, 195) transparent transparent;
	}

	.table {
		width: 100%;
		border-spacing: 0;
		border-collapse: collapse;
