							color: #909399;
							font-size: 40px;
						}
					}

					.count {
						width: 100%;
						height: 100%;
						color: #FFFFFF;
						font-size: 18px;
						line-height: 51px;
						position: relative;

						&:hover ul {
							display: block;
						}

						ul {
							position: absolute;
							left: -4px;
							bottom: 40px;
							background: linear-gradient(to top, #7297E8, #4C84FF);
							border-radius: 4px;
							width: calc(100% + 8px);
							padding-bottom: 7px;
							padding-top: 7px;
							display: none;

							li {
								color: #FFFFFF;
								line-height: 24px;
							}
