							width: 280px;
							height: 76px;
							border-radius: 6px;
							background: rgba(255,255,255,1);
							box-shadow: 0px 0px 8px 0px rgba(23,38,71,0.16);
							padding: 16px;
							display: none;
							
							.flex{
								&:nth-child(1){
									div{
										&:nth-child(1){
											color: rgb(76, 132, 255);
										}
										&:nth-child(2){
											color: rgb(76, 132, 255);
										}
									}
								}
								&:nth-child(2){
									div{
										color: #303133;
										margin-right: 10px;
										
										&:last-child{
											margin-right: 0;
										}
									}
								}
							}
						}

						.course-jump {
							position: absolute;
							left: 0;
							right: 0;
							top: 0;
							bottom: 0;
							background-color: rgba(0, 0, 0, .56);
							display: none;
							justify-content: space-around;
							align-items: center;

							i {
								color: #FFFFFF;
								font-size: 28px;
							}
						}
					}

					.my-calendar {
						margin: 10px;
						position: relative;
						border-radius: 4px;
						width: calc(100% - 20px);
						height: calc(100% - 20px);
						padding: 12px 8px;

						&.bg1 {
							background: rgb(76, 132, 255);
						}
						&.bg2 {
							background: rgb(253, 164, 35);
						}

						&.bg3 {
							background: rgb(183, 187, 195);
						}
						
						div{
							color: #FFFFFF;
							text-align: left;
						}
						
						.course-detail-col{
							position: absolute;
							right: -280px;
							top: 0;
							z-index: 99;
							width: 280px;
							border-radius: 6px;
							background: rgba(255,255,255,1);
							box-shadow: 0px 0px 8px 0px rgba(23,38,71,0.16);
							padding: 16px;
							display: none;
							
							.entry{
								background: rgb(76, 132, 255);
								display: inline-block;
								padding: 2px 7px;
								cursor: pointer;
								border-radius: 4px;
								margin-top: 3px;
							}
							
							.flex{
								&:nth-child(1){
									div{
										&:nth-child(1){
											color: rgb(76, 132, 255);
										}
										&:nth-child(2){
											color: rgb(76, 132, 255);
										}
									}
								}
								&:nth-child(2){
									div{
										color: #303133;
										margin-right: 10px;
										
										&:last-child{
											margin-right: 0;
										}
									}
								}
							}
						}
					}
					
					.school-detail{
						.school-detail-1{
							background-color: red;
							position: absolute;
							left: 5px;
							top: 5px;
							right: 15px;
							bottom: 15px;
							display: flex;
							flex-direction: column;
							justify-content: center;
							z-index: 2;
							border-radius: 6px;
							
							div{
								color: #FFFFFF;
							}
						}
						
						.school-detail-2{
							position: absolute;
							left: 15px;
							top: 15px;
							right: 5px;
							bottom: 5px;
							z-index: 1;
							border-radius: 6px;
						}
						
						.course-detail-col{
							position: absolute;
							right: -280px;
							top: 0;
							z-index: 99;
							width: 280px;
							border-radius: 6px;
							background: rgba(255,255,255,1);
							box-shadow: 0px 0px 8px 0px rgba(23,38,71,0.16);
							padding: 16px;
							display: none;
							
							ul{
								li{
									&:not(:last-child){
										border-bottom: 1px solid #EBEEF5;
										padding-bottom: 16px;
										margin-bottom: 16px;
									}
									
									div{
										display: flex;
										
										.course-entry{
											background-color: #4C84FF;
											display: inline-block;
											padding: 0 2px;
											line-height: 17px;
											color: #FFFFFF!important;
											margin-top: 5px;
											border-radius: 4px;
											padding: 2px 5px;
										}
										
										&:first-child{
											&:first-child{
												color:#4C84FF;
												margin-right: 10px;
											}
										}
										
										&:nth-child(2){
											div{
												&:not(:last-child){
													margin-right: 10px;
												}
											}
											
											.sync-icon{
												color: rgb(76, 132, 255);
												background: rgba(76, 132, 255, .4);
												line-height: 17px;
												padding: 0 2px;
												border-radius: 4px;
											}
											
											.rec-icon{
												color: rgb(253, 164, 35);
												background: rgba(253, 164, 35, .4);
												line-height: 17px;
												padding: 0 2px;
												border-radius: 4px;
											}
										}
									}
								}
							}
							.flex{
								&:nth-child(1){
									div{
										&:nth-child(1){
											color: rgb(76, 132, 255);
										}
										&:nth-child(2){
											color: rgb(76, 132, 255);
										}
									}
								}
								&:nth-child(2){
									div{
										color: #303133;
										margin-right: 10px;
										
										&:last-child{
											margin-right: 0;
										}
									}
								}
							}
